      &.active {
        display: flex; 
      }

      &-item {
        margin: var(--spacing-small) 0; 
        width: 100%;
        text-align: center;
      }

      &-item:last-child {
        margin-top: var(--spacing-medium); 
      }

      &-link {
        display: block; 
        padding: var(--spacing-small) 0; 
      }
